
*{
  transition-property: all;
  transition-duration: 1s;
  -o-transition-timing-function: ease-in-out;
  scroll-behavior: smooth;
}
#todowrap{
  display:block ;
  min-height : 100vh;
  place-items: center;
}

::-webkit-scrollbar {
  width: 5px;
  background-color: #0d6efd33;
  border-radius: 60px;
}
::-webkit-scrollbar-thumb {
  background-color: #4791ff;
  border-radius: 50px;
}